.footer{
	background-color: #0D0D0D;
	&-container{
		@extend .container;
		padding: 30px 0;
	}
	&-row{
		@include make-row();
	}
	&-col{
		@include make-col-ready($grid-columns);
		@include make-col(4);
		text-align: center;
	}
	&-ttl{
		font-size: 12px;
		font-weight: bold;
		text-transform: uppercase;
		color: #fff;
	}
	&-lnk{
		font-size: 12px;
		color: #fff;
		&:hover{
			text-decoration: underline;
		}
	}
	&-soc{
		@extend .header-soc; 
		color: #fff;
		&-blk{
			text-align: center;
		}
	}
	&-logo{
		display: block;
		max-width: 110px;
		margin: 0 auto;
		font-size: 40px;
		color: #fff;
		text-transform: uppercase;
	}
}

@media screen and (max-width: 480px){
	.footer{
		&-col{
				@include make-col(6);
				&.__last{
					@include make-col(12);
					padding-bottom: 10px;
				}
		}
	}
}