.category{
	padding-bottom: 25px;

	&-sidebar{
		&-txt{
			@extend .product-txt;
			padding: 10px 0;
			font-weight: 700;
		}
	}
	&-row{
		@include make-row();
	}
	&-col{
		@include make-col-ready($grid-columns);
		@include make-col(3);
		display: flex;
	}
	&-ttl{
		@extend .ttl;
		&-L{
			@extend .ttl-L;
		}
	}
	&-btn{
		&-show{
			@extend .btn-show;
			padding-bottom: 0;
			font-size: 24px;
			&-wrap{
				text-align: center;
			}
		}
		&-inverse{
			@extend .btn-inverse;
			&-wrap{
				max-width: 170px;
				padding-top: 20px;
			}
		}
	}
	&-c{
		@extend .product-txt;
		&-inp{
			@extend .inp-c;
			&:checked+&-lbl {
			    box-shadow: 0 0 0 1px #fff, 0 0 0 2px black;  
			}
			&-lbl{
				@extend .inp-c-lbl;
			}
		}
		&-wrap{
			@extend .inp-c-wrap;
		}
	}
}

@media screen and (max-width: 900px){
	.category{
		&-col{
			@include make-col(4);
		}
	}
}

@media screen and (max-width: 680px){
	.category{
		&-col{
			@include make-col(6);
		}
		&-sidebar{
			&-txt{
				padding: 0;
			}
		}
		&-btn{
			&-inverse{
				&-wrap{
					max-width: 100%;
				}
			}
		}
	}
}